const Event = {
  CONTACT_SERVER_ERROR: 'bb.event.contact-approvals.server.error',
};

export default ({ notifications, $filter }) => {
  const i18n = $filter('i18n');
  return {
    [Event.CONTACT_SERVER_ERROR]() {
      notifications.notifyAlert(i18n('notification.server.error'));
    },
  };
};



